import React from 'react';
import PropTypes from 'prop-types';

import EmotionButton from './EmotionButton';

import { getResult, postResult } from '../utils/result-handler';

const ExtraQuestion = ({
  responses,
  emotion,
  addUserExtraResponse,
  createResult,
  history
}) => {
  const onclick = (e) => {
    e.preventDefault();

    const result = getResult(responses);

    addUserExtraResponse(e.target.dataset.emotion);
    createResult(result);
    postResult(responses, emotion);

    history.push('/tape');
  };

  return (
    <div className="container full-width">
      <div className="row margin-top-xl">
        <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
          <div className="dialog dialog-bubble dialog-md horizontal-center">
            <h2 className="margin-no">以下哪個情緒容易讓你購物？</h2>
          </div>
        </div>
      </div>
      <div className="row margin-top-md-c">
        <div className="col-xs-5 col-xs-offset-1 col-sm-2 col-sm-offset-2 col-md-c col-md-offset-c text-right text-center-sm">
          <EmotionButton emotion="happy" display="開心" onclick={onclick} />
        </div>
        <div className="col-xs-5 col-sm-2 col-md-c  text-left text-center-sm">
          <EmotionButton emotion="sad" display="難過" onclick={onclick} />
        </div>
        <div className="col-xs-5 col-sm-2 col-xs-offset-1 col-sm-offset-0 col-md-c text-right text-center-sm">
          <EmotionButton emotion="annoyed" display="煩躁" onclick={onclick} />
        </div>
        <div className="col-xs-5 col-sm-2 col-md-c text-center text-left text-center-sm">
          <EmotionButton emotion="bored" display="無聊" onclick={onclick} />
        </div>
      </div>
    </div>
  );
};

ExtraQuestion.propTypes = {
  responses: PropTypes.arrayOf(
    PropTypes.object.isRequired
  ).isRequired,
  emotion: PropTypes.string.isRequired,
  createResult: PropTypes.func.isRequired,
  addUserExtraResponse: PropTypes.func.isRequired,
  history: PropTypes.shape().isRequired
};

export default ExtraQuestion;



