import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Scroll from 'react-scroll';
import classNames from 'classnames';

import { banners, figures } from '../utils/image-handler';
import initFacebookShare from '../utils/facebook-handler';

import circleImg from '../images/final-2.svg';

class Final extends Component {
  static shareOnFacebook(href) {
    window.FB.ui({
      method: 'share',
      href,
      mobile_iframe: true
    }, () => { /* do nothing */ });
  }

  constructor() {
    super();

    this.activateButton = this.activateButton.bind(this);
    this.deactivateButton = this.deactivateButton.bind(this);

    this.state = {
      isButton0Active: false,
      isButton1Active: false,
      isButton2Active: false,
      isButton3Active: false
    };
  }

  componentDidMount() {
    initFacebookShare();
  }

  activateButton(e) {
    const id = e.target.dataset.btnid;
    this.setState({
      [`isButton${id}Active`]: true
    });
  }

  deactivateButton() {
    this.setState({
      isButton0Active: false,
      isButton1Active: false,
      isButton2Active: false,
      isButton3Active: false
    });
  }

  render() {
    const { result } = this.props;

    const Element = Scroll.Element;
    const scroller = Scroll.scroller;

    return (
      <div className="container full-width full-height" id="container">
        <div className="container horizontal-center full-width height-2">
          <div className="row">
            <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
              <img
                src={banners[result.bannerImage]}
                className="img-responsive img-lg-title"
                alt={result.title}
              />
            </div>
          </div>
        </div>
        {/* result */}
        <div className="container full-width height-8">
          <div className="vertical-horizontal-center full-width">
            <div className="row">
              <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
                <img
                  src={figures[result.figureImage]}
                  className="img-responsive img-lg-title"
                  alt={result.caption}
                />
              </div>
            </div>
            <div className="row margin-top-lg">
              <div className="col-xs-5 col-xs-offset-1 col-sm-3 col-sm-offset-3 col-md-2 col-md-offset-4 text-right">
                <button
                  data-btnid="0"
                  className={classNames(
                    'btn',
                    'btn-default',
                    'btn-lg',
                    'margin-right',
                    { 'btn-active': this.state.isButton0Active }
                  )}
                  onMouseDown={this.activateButton}
                  onDragEnd={this.deactivateButton}
                  onTouchStart={this.activateButton}
                  onTouchEnd={this.deactivateButton}
                  onTouchCancel={this.deactivateButton}
                  onMouseUp={() => {
                    this.deactivateButton();
                    scroller.scrollTo('anchor', {
                      duration: 500,
                      delay: 100,
                      smooth: true,
                      containerID: 'container'
                    });
                  }}
                >
                  救命箴言
                </button>
              </div>
              <div className="col-xs-5 col-sm-3 col-md-2 text-left">
                <button
                  data-btnid="1"
                  data-type="princess"
                  className={classNames(
                    'btn',
                    'btn-default',
                    'btn-lg',
                    'margin-left',
                    { 'btn-active': this.state.isButton1Active }
                  )}
                  onMouseDown={this.activateButton}
                  onDragEnd={this.deactivateButton}
                  onTouchStart={this.activateButton}
                  onTouchEnd={this.deactivateButton}
                  onTouchCancel={this.deactivateButton}
                  onMouseUp={(e) => {
                    this.deactivateButton();
                    Final.shareOnFacebook(
                      result[`fb_share_href_${e.target.dataset.type}`]
                    );
                  }}
                >
                  分享死法
                </button>
              </div>
            </div>
          </div>
        </div>
        <Element name="anchor" />
        {/* quote */}
        <div className="container full-width full-height background-white">
          {/* decoration */}
          <div className="deco" />
          {/* content */}
          <div className="vertical-horizontal-center full-width">
            <div className="row">
              <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
                <div className="dialog dialog-red horizontal-center">
                  <h2 className="margin-no">
                    {result.quote}
                  </h2>
                  <p className="margin-no margin-top-md">
                    - {result.quote_from} -
                  </p>
                </div>
              </div>
            </div>
            <div className="row margin-xs-top-lg margin-sm-top-lg">
              <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
                <div className="dialog horizontal-center">
                  <p className="margin-no padding-sm">
                    {result.wisdom}
                  </p>
                </div>
              </div>
            </div>
            <div className="row margin-xs-top-lg margin-sm-top-lg">
              <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
                <img
                  src={circleImg}
                  className="img-responsive img-sm"
                  alt="Figure"
                />
              </div>
            </div>
            <div className="row margin-xs-top-lg margin-sm-top-lg">
              <div className="col-xs-5 col-xs-offset-1 col-sm-3 col-sm-offset-3 col-md-2 col-md-offset-4 text-right">
                <button
                  data-btnid="2"
                  data-type="quote"
                  className={classNames(
                    'btn',
                    'btn-default',
                    'btn-lg',
                    'margin-right',
                    { 'btn-active': this.state.isButton2Active }
                  )}
                  onMouseDown={this.activateButton}
                  onDragEnd={this.deactivateButton}
                  onTouchStart={this.activateButton}
                  onTouchEnd={this.deactivateButton}
                  onTouchCancel={this.deactivateButton}
                  onMouseUp={(e) => {
                    this.deactivateButton();
                    Final.shareOnFacebook(
                      result[`fb_share_href_${e.target.dataset.type}`]
                    );
                  }}
                >
                  分享箴言
                </button>
              </div>
              <div className="col-xs-5 col-sm-3 col-md-2 text-left">
                <button
                  data-btnid="3"
                  data-type="game"
                  className={classNames(
                    'btn',
                    'btn-default',
                    'btn-lg',
                    'margin-left',
                    { 'btn-active': this.state.isButton3Active }
                  )}
                  onMouseDown={this.activateButton}
                  onDragEnd={this.deactivateButton}
                  onTouchStart={this.activateButton}
                  onTouchEnd={this.deactivateButton}
                  onTouchCancel={this.deactivateButton}
                  onMouseUp={(e) => {
                    this.deactivateButton();
                    Final.shareOnFacebook(
                      result[`fb_share_href_${e.target.dataset.type}`]
                    );
                  }}
                >
                  分享遊戲
                </button>
              </div>
            </div>
            <div className="row margin-xs-top-lg margin-sm-top-lg">
              <div className="col-xs-10 col-xs-offset-1 col-sm-6 col-sm-offset-3 col-md-4 col-md-offset-4 text-center">
                <div className="logo" />
              </div>
            </div>
          </div>
          <div className="footer full-width background-yellow" />
        </div>
      </div>
    );
  }
}

Final.propTypes = {
  result: PropTypes.shape().isRequired
};

export default Final;



