import React, { Component } from 'react';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import classNames from 'classnames';

import Wait from './Wait';
import ExtraQuestion from './ExtraQuestion';

class Questionnaire extends Component {
  constructor(props) {
    super(props);

    this.activateButton = this.activateButton.bind(this);
    this.deactivateButton = this.deactivateButton.bind(this);

    this.state = {
      isButtonPressed: false,
      isButton0Active: false,
      isButton1Active: false
    };
  }

  activateButton(e) {
    const targetScore = e.target.dataset.score;
    this.setState({
      isButton0Active: targetScore === '-1',
      isButton1Active: targetScore === '1'
    });
  }

  deactivateButton() {
    this.setState({
      isButton0Active: false,
      isButton1Active: false
    });
  }

  render() {
    const {
      tests,
      responses,
      emotion,
      collectResponse,
      createResult,
      addUserExtraResponse,
      match,
      history
    } = this.props;

    const testId = match.params.testId;
    const questions = tests[parseInt(testId, 10) - 1];
    const nextTestId = testId === '9' ? 'wait' : parseInt(testId, 10) + 1;

    if (testId === 'wait') {
      return <Wait history={history} />;
    } else if (testId === '10') {
      return (
        <ExtraQuestion
          createResult={createResult}
          addUserExtraResponse={addUserExtraResponse}
          history={history}
          responses={responses}
          emotion={emotion}
        />
      );
    }

    return (
      <div className="container full-width fixed">
        <div className="container">
          <div className="row margin-top-lg">
            {questions.map((q, i) =>
              <div
                key={shortid.generate()}
                className={classNames(
                  'col-xs-5',
                  'col-sm-3',
                  // 'col-md-2',
                  'text-center',
                  { 'col-xs-offset-1': i === 0 },
                  { 'col-sm-offset-3': i === 0 },
                  // { 'col-md-offset-4': i === 0 }
                )}
              >
                <button
                  className={classNames(
                    'btn',
                    'btn-round',
                    { 'btn-active': this.state[`isButton${i}Active`] }
                  )}
                  data-tag={q.tag}
                  data-score={i === 0 ? -1 : 1}
                  onMouseDown={this.activateButton}
                  onDragEnd={this.deactivateButton}
                  onTouchStart={this.activateButton}
                  onTouchCancel={this.deactivateButton}
                  onTouchEnd={this.deactivateButton}
                  onMouseUp={
                    (e) => {
                      if (!this.state.isButtonPressed) {
                        this.setState({ isButtonPressed: true });

                        const tag = e.target.dataset.tag;
                        const score = parseInt(e.target.dataset.score, 10);
                        const end = (new Date()).getTime();

                        let isTheLastTest = false;

                        if (parseInt(testId, 10) === tests.length) {
                          isTheLastTest = true;
                        }

                        collectResponse({
                          testId: parseInt(testId, 10),
                          tag,
                          end,
                          score
                        }, isTheLastTest);

                        this.deactivateButton();

                        history.push(`/test/${nextTestId}`);
                      }
                    }
                  }
                >
                  {q.text}
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

Questionnaire.propTypes = {
  tests: PropTypes.arrayOf(
    PropTypes.array.isRequired
  ).isRequired,
  responses: PropTypes.arrayOf(
    PropTypes.object.isRequired
  ).isRequired,
  emotion: PropTypes.string.isRequired,
  collectResponse: PropTypes.func.isRequired,
  addUserExtraResponse: PropTypes.func.isRequired,
  createResult: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.object.isRequired
  }).isRequired,
  history: PropTypes.shape().isRequired
};

export default Questionnaire;



