import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

class EmotionButton extends Component {
  constructor(props) {
    super(props);

    this.activateButton = this.activateButton.bind(this);
    this.deactivateButton = this.deactivateButton.bind(this);

    this.state = {
      isButtonActive: false
    };
  }

  activateButton() {
    this.setState({
      isButtonActive: true
    });
  }

  deactivateButton() {
    this.setState({
      isButtonActive: false
    });
  }

  render() {
    const { emotion, display, onclick } = this.props;

    return (
      <button
        id={`btn-${emotion}`}
        data-emotion={emotion}
        className={classNames(
          'btn',
          'btn-circle',
          { 'btn-active': this.state.isButtonActive }
        )}
        onMouseDown={this.activateButton}
        onDragEnd={this.deactivateButton}
        onTouchStart={this.activateButton}
        onTouchEnd={this.deactivateButton}
        onTouchCancel={this.deactivateButton}
        onMouseUp={(e) => {
          this.deactivateButton();
          onclick(e);
        }}
      >
        <span>{display}</span>
      </button>
    );
  }
}

EmotionButton.propTypes = {
  emotion: PropTypes.string.isRequired,
  display: PropTypes.string.isRequired,
  onclick: PropTypes.func.isRequired
};

export default EmotionButton;



